.block {
  position: relative;
  margin-bottom: 0px;
}

.block svg {
  z-index: 1; /* Устанавливаем z-index для SVG равный 1 */
  margin-bottom: 228px;
}

.block__content {
  position: absolute;
  z-index: 2; /* Устанавливаем z-index для .block__content выше, чем для SVG */
}

.block__text {
  position: absolute;
  top: 80px;
  left: 43px;
  z-index: 3;
  margin: 0 auto 0px 0; /* Выравниваем текст по левому краю и добавляем отступ справа */
  text-align: left; /* Выравниваем текст по левому краю */
  max-width: 211px;
  padding-left: px; /* Добавляем отступ справа в 413px */
}


.block__title {
  margin-bottom: 37px;
  font-weight: 700;
  font-size: 32px;
  line-height: 75%;
  color: #000;
  white-space: nowrap; 
}

.block__title2 {
  margin-bottom: 13px;
  font-weight: 700;
  font-size: 24px;
  color: #000;
}

.block__desc1 {
  font-weight: 400;
  font-size: 18px;
  color: #000;
