.header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1; /* Устанавливаем z-index, чтобы Header располагался выше SVG-фона */
    padding: 90px 43px 0px 0px;

  }
.header_row {
    display: flex;
    column-gap: 30px;
    align-items:center;
    justify-content:space-between;
}

.header_nav {
    color: var(--textColor);
    font-weight: 500;
font-size: 18px;
line-height: 24px;
letter-spacing: normal;
}


.header_nav ul {
    display: flex;
    column-gap: 25px;
    align-items: center;
}

.header_nav-btn{
    padding: 20px 45px 20px;
    background-color: black;
    border-radius: 22px;

    font-weight: 500;
font-size: 18px;
line-height: 24px;
letter-spacing: normal;
color: white;
}
