.promo {
    position: relative; /* Устанавливаем относительное позиционирование для родительского элемента */
  }
  
  .promo_content {
    position: absolute; /* Добавляем относительное позиционирование для promo_content */
    top: 221px; /* Добавляем отступ сверху для promo_content, соответствующий отступу SVG-фона */
    left: 650px; /* Добавляем отступ слева для promo_content, соответствующий отступу SVG-фона */
    right: 112px; /* Добавляем отступ справа для promo_content, соответствующий отступу SVG-фона */
    z-index: 1; /* Устанавливаем z-index, чтобы promo_content располагался выше SVG-фона */
  }
  
  svg {
    padding-top: 50px;
    width: 1181px;
    height: auto;
  }

  .promo_text1 {
    max-width: 416px;
    font-weight: 500;
    font-size: 24px;
    line-height: 10px;
    color: #000000;
    text-align: center;
  }
  
  .promo_text2 {
    max-width: 416px;
    font-weight: 900;
    font-size: 64px;
    line-height: 100px;
    color: #000000;
    text-align: center;
  }
  
  .promo_text3 {
    max-width: 416px; /* Добавляем максимальную ширину для promo_text3 */
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: #000000;
    text-align: center;
    
    
  }
  